("Los sistemas de mejoramiento de la gestión por procesos, son enfoques estructurados y metodologías que buscan optimizar el desempeño organizacional mediante la identificación, análisis y mejora continua de los procesos claves. Su objetivo es garantizar eficiencia, calidad y alineación con los objetivos estratégicos de la organización.")]),_vm._m(22),_vm._m(23),_vm._m(24),_c('figcaption',{staticClass:"mt-2 text-center mb-4"},[_vm._v("Nota. Basado en Angel Maldonado, J. (2012). ")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("A continuación, ampliemos la información sobre cada uno de ellos:")]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"id":"acordeon-3"}},[_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Ciclo de mejora continua (PDCA o Deming)"}},[_c('p',[_c('strong',[_vm._v("Plan (Planear).")]),_vm._v(" Identificación de problemas y planificación de mejoras.")]),_c('p',[_c('strong',[_vm._v("Do (Hacer).")]),_vm._v(" Implementación de las mejoras en un entorno controlado.")]),_c('p',[_c('strong',[_vm._v("Check (Verificar).")]),_vm._v(" Evaluación de los resultados obtenidos.")]),_c('p',[_c('strong',[_vm._v("Act (Actuar).")]),_vm._v(" Ajuste y estandarización de mejoras exitosas.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Gestión de la Calidad Total (Total Quality Management - TQM)"}},[_c('p',[_vm._v("Es un enfoque integral que busca mejorar de manera continua todos los procesos dentro de una organización, con el objetivo de satisfacer las necesidades del cliente y optimizar la eficiencia operativa. Se basa en principios como la orientación al cliente, el liderazgo comprometido, la participación de todos los empleados y la toma de decisiones basada en datos. Utiliza herramientas como el control estadístico de calidad, que permite analizar la variabilidad en los procesos; el benchmarking, para identificar y adoptar mejores prácticas de otras organizaciones; y los círculos de calidad, donde grupos de trabajo colaboran en la identificación y resolución de problemas dentro de sus áreas.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Seis Sigma (6 σ) o Six Sigma"}},[_c('p',[_vm._v("Es una metodología basada en el análisis de datos para reducir la variabilidad y los defectos en los procesos, con el objetivo de alcanzar niveles de calidad cercanos a la perfección. Se fundamenta en la estrategia DMAIC (Definir, Medir, Analizar, Mejorar y Controlar) para optimizar procesos existentes y en DMADV (Definir, Medir, Analizar, Diseñar y Verificar), cuando se requiere desarrollar procesos nuevos. Seis Sigma emplea herramientas estadísticas avanzadas, como el análisis de causa raíz y diagramas de Pareto, además de fomentar una cultura de mejora estructurada a través de la formación de expertos con distintos niveles de certificación (Green Belt, Black Belt y Master Black Belt).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Lean Management"}},[_c('p',[_vm._v("Es un enfoque de gestión que busca maximizar el valor para el cliente eliminando desperdicios y optimizando la eficiencia operativa. Se basa en la identificación y reducción de actividades que no agregan valor dentro de los procesos, utilizando herramientas como Kaizen (mejora continua mediante pequeñas acciones diarias), 5S (metodología de organización del espacio de trabajo), Kanban (sistema visual para gestionar el flujo de trabajo) y Value Stream Mapping (VSM), que permite mapear y analizar procesos para identificar oportunidades de mejora. Lean Management es ampliamente aplicado en manufactura y servicios, ayudando a reducir costos, mejorar la calidad y aumentar la agilidad organizacional.")])])])],1),_vm._m(25)]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"id":"acordeon-4"}},[_vm._m(26),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"ISO 9001"}},[_c('p',[_vm._v("Junto con otras normas de gestión, establecen lineamientos para la estandarización, control y mejora de los procesos organizacionales. La norma ISO 9001, en particular, se centra en la gestión de la calidad, estableciendo requisitos para la documentación, auditoría y mejora continua de los procesos. Su implementación permite a las organizaciones garantizar la conformidad con estándares internacionales, mejorar la satisfacción del cliente y fortalecer la cultura de calidad. Además de ISO 9001, existen otras normas como ISO 14001 (gestión ambiental), ISO 45001 (seguridad y salud en el trabajo) e ISO 27001 (seguridad de la información), que complementan la gestión empresarial con un enfoque integral.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"<em>Benchmarking</em>"}},[_c('p',[_vm._v("Es una herramienta de mejoramiento que consiste en la comparación de los procesos propios con los de organizaciones líderes en el mercado, con el fin de identificar mejores prácticas y adaptarlas a la propia realidad. Puede realizarse en diferentes niveles: interno (comparación entre áreas dentro de la misma organización), competitivo (comparación con competidores directos), funcional (comparación con organizaciones de distintos sectores, pero con procesos similares) o genérico (identificación de prácticas innovadoras en cualquier industria). Benchmarking permite obtener referencias de desempeño, detectar oportunidades de mejora y fomentar la innovación a partir del aprendizaje de otros.")])])])],1)]),_vm._m(27),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Para conocer sobre esta temática, lo invitamos a ver el siguiente video:")]),_vm._m(28),_c('div',{staticClass:"bg-full-width border-top actividad bg-color-actividad"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{attrs:{"id":"Actividad"}},[_c('Actividad',{attrs:{"cuestionario":_vm.cuestionario}})],1)])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"